import React from 'react';
import { Helmet } from 'react-helmet';
import Menubar from '../Menubar';
import QuickLinks from '../QuickLinks';
import Footer from '../Footer';
import ContactContent from './content/ContactContent';

const ContactPage = () => (
    <div>
        <Helmet>
            <title>Contact us | College of agriculture Konghara</title>
        </Helmet>
        <Menubar/>
        <div className="container">
            <div className="row">
                <div className="col-md-8 BackWhite">
                    <ContactContent/>
                </div>
                <div className="col-md-3 col-md-offset-1 BackWhite hidden-xs">
                 <QuickLinks/>
                </div>
            </div>
        </div>
        <Footer/>
    </div>
);


export default ContactPage;


