import React from 'react';
import {BrowserRouter, Route, Switch } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import HomePage from '../components/pages/HomePage';
import AboutPage from '../components/pages/AboutPage';
import PrincipleMessagePage from '../components/pages/PrincipleMessagePage';
import CoursesPage from '../components/pages/CoursesPage';
import AdmissionPage from '../components/pages/AdmissionPage';
import FacilitiesPage from '../components/pages/FacilitiesPage';
import StudentSectionPage from '../components/pages/StudentSectionPage';
import PhotoGalleryPage from '../components/pages/PhotoGalleryPage';
import ContactPage from '../components/pages/ContactPage';



const AppRouter = (
    <BrowserRouter basename="argireact">
        <div>
            <Helmet>
                <script src="https://ajax.googleapis.com/ajax/libs/jquery/3.3.1/jquery.min.js"></script>
                <script src="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.2/js/bootstrap.min.js"></script>
            </Helmet>
          <Switch>
                <Route path="/" component={HomePage} exact={true}/>
                <Route path="/about" component={AboutPage} exact={true}/>
                <Route path="/principle-message" component={PrincipleMessagePage} exact={true}/>
                <Route path="/courses" component={CoursesPage} exact={true}/>
                <Route path="/admission" component={AdmissionPage} exact={true}/>
                <Route path="/facilities" component={FacilitiesPage} exact={true}/>
                <Route path="/student-section" component={StudentSectionPage} exact={true}/>
                <Route path="/photo-gallery" component={PhotoGalleryPage} exact={true}/>
                <Route path="/contact-us" component={ContactPage} exact={true}/>




            </Switch>
        </div>
        
    </BrowserRouter>
);

export default AppRouter;


