import React from 'react';

const LocationMap = () => (
    <iframe 
        src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3747.3376192755686!2d78.53972991433291!3d20.078168824924234!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bd3aeeaf549b689%3A0x3979f9a7e37b9056!2sCollege+Of+Agriculture+Konghara!5e0!3m2!1sen!2sin!4v1530125653260" 
        width="100%" 
        height="200px" 
        frameBorder="0" 
        title="Location"
        allowFullScreen>
    </iframe>

);

const AddressDetails = () => (
    <div className="row" id="email">
        <div className="col-md-4 text-center">
            <h1><span className="glyphicon glyphicon-map-marker"></span></h1>
            <h2>Address</h2>
            <p>Konghara - 445302, tq-Kelapur, dist-Yavatmal</p>
        </div>

        <div className="col-md-4 text-center">
            <h1><span className="glyphicon glyphicon-earphone"></span></h1>
            <h2>Phone</h2>
            <p>9960669468, 9970874535, 9657840357</p>
        </div>

        <div className="col-md-4 text-center">
            <h1><span className="glyphicon glyphicon-envelope"></span></h1>
            <h2>Email</h2>
            <p>agrico.konghara@gmail.com</p>
        </div>
       
    </div>
);

const ContactContent = () => (
    <div>
        <h4>Contact Details</h4>
        <hr className="Newhr"/>
        <LocationMap/><br/><br/>
        <AddressDetails/>
    </div>
);

export default ContactContent;


