import React from 'react';
import { Helmet } from 'react-helmet';
import Menubar from '../Menubar';
import QuickLinks from '../QuickLinks';
import Footer from '../Footer';


const HomePage = () => (
    <div>
        <Helmet>
            <title>Welcome | College of agriculture Konghara</title>
        </Helmet>
        <Menubar/>
        <div className="container">
            <div className="row">
                <div className="col-md-8 BackWhite">Home</div>
                <div className="col-md-3 col-md-offset-1 hidden-xs BackWhite">
                 <QuickLinks/>
                </div>
            </div>
        </div>
        <Footer/>
    </div>
);


export default HomePage;



