import React from 'react';
import { Helmet } from 'react-helmet';
import Menubar from '../Menubar';
import QuickLinks from '../QuickLinks';
import Footer from '../Footer';


const StudentSectionPage = () => (
    <div>
        <Helmet>
            <title>Student Section | College of agriculture Konghara</title>
        </Helmet>
        <Menubar/>
        <div className="container">
            <div className="row">
                <div className="col-md-8 BackWhite">
                    <h4>Student Section</h4>
                    <hr className="Newhr"/>
                        <ul>
                            <li>College has got student forum</li>
                            <li>Students participate in NSS activities</li>
                            <li>Student guides the farmers for improving crop cultivation</li>
                        </ul>
                </div>
                <div className="col-md-3 col-md-offset-1 hidden-xs BackWhite">
                 <QuickLinks/>
                </div>
            </div>
        </div>
        <Footer/>
    </div>
);


export default StudentSectionPage;


