import React from 'react';
import { NavLink } from 'react-router-dom';

const Menubar = () => (
    <nav className="navbar navbar-default navbar-fixed-top">
        <div className="container">
            <div className="navbar-header">
            <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1">
                <span className="sr-only">Toggle navigation</span>
                <span className="icon-bar"></span>
                <span className="icon-bar"></span>
                <span className="icon-bar"></span>
            </button>
                <NavLink to="/" exact={true} className="navbar-brand"><span className="glyphicon glyphicon-home"></span></NavLink>
            </div>

            <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
                <ul className="nav navbar-nav">
                    <li><NavLink to="/about" exact={true} activeClassName="ActiveLink">About</NavLink></li>
                    <li><NavLink to="/principle-message" exact={true} activeClassName="ActiveLink">Principle Message</NavLink></li>
                    <li><NavLink to="/courses" exact={true} activeClassName="ActiveLink">Courses</NavLink></li>
                    <li><NavLink to="/admission" exact={true} activeClassName="ActiveLink">Admission</NavLink></li>
                    <li><NavLink to="/facilities" exact={true} activeClassName="ActiveLink">Facilities</NavLink></li>
                    <li><NavLink to="/student-section" exact={true} activeClassName="ActiveLink">Student Section</NavLink></li>
                    <li><NavLink to="/photo-gallery" exact={true} activeClassName="ActiveLink">Photo Gallery</NavLink></li>
                    <li><NavLink to="/contact-us" exact={true} activeClassName="ActiveLink">Contact Us</NavLink></li>
                </ul>
            </div>
        </div>
    </nav>
);

export default Menubar;


